import { createStore } from "vuex";

export default createStore({
    state: {
        selectedFile: null,
    },
    mutations: {
        setNewFile(state, payload) {
            state.selectedFile = payload.file
        },
    },
    actions: {},
    modules: {},
    getters: {
        getSelectedFile: state => {
            return state.selectedFile
        }
    },
});